.no-matching {

    @include flex-center(column, nowrap);
    gap: 1.5rem;

    h2 {
        font-size: 2.15rem;
    }

    a {
        font-size: 2.85rem;
        text-decoration: underline;
        transition: 0.15s all;

        &:hover {
            color: $theme-color;;
        }
    }

}